import {useAppContext} from '../../AppContext';

type Props = {
    isFirst: boolean,
    isPaid: boolean
};
const CTABox: React.FC<Props> = ({isPaid}) => {
    const {member, t} = useAppContext();

    const handleSignUpClick = () => {
        window.location.href = (isPaid && member) ? '#/portal/account/plans' : '#/portal/signup';
    };

    return (
        <section className={`mx-[20px] flex flex-col`} data-testid="cta-box">
            <button className="font-san mb-[12px] inline-block rounded-[8px] bg-white px-5 py-[24px] text-[16px] font-normal leading-none tracking-widest text-black transition-all hover:opacity-90" data-testid="signup-button" type="button" onClick={handleSignUpClick}>
                {t('今すぐ入部してコメントする')}
            </button>
        </section>
    );
};

export default CTABox;
