import Blockquote from '@tiptap/extension-blockquote';
import Document from '@tiptap/extension-document';
import HardBreak from '@tiptap/extension-hard-break';
import Link from '@tiptap/extension-link';
import Paragraph from '@tiptap/extension-paragraph';
import Placeholder from '@tiptap/extension-placeholder';
import Text from '@tiptap/extension-text';
import {EditorOptions, Mark, mergeAttributes} from '@tiptap/core';

// RedTextマークの定義
const RedText = Mark.create({
    name: 'redText',

    parseHTML() {
        return [
            {
                tag: 'span[style="color: red;"]',
            }
        ];
    },

    renderHTML({HTMLAttributes}) {
        return ['span', mergeAttributes(HTMLAttributes, {style: 'color: red;'}), 0];
    },

    addCommands() {
        return {
            setRedText: () => ({commands}) => {
                return commands.setMark(this.name);
            },
            unsetRedText: () => ({commands}) => {
                return commands.unsetMark(this.name);
            }
        };
    }
});

export function getEditorConfig({placeholder, autofocus = false, content = ''}: {placeholder: string; autofocus?: boolean; content?: string}): Partial<EditorOptions> {
    return {
        extensions: [
            Document,
            Text,
            Paragraph,
            Link.configure({
                openOnClick: false
            }),
            Placeholder.configure({
                placeholder,
                showOnlyWhenEditable: false
            }),
            Blockquote.configure({}),

            // Enable shift + enter to insert <br> tags
            HardBreak.configure({}),

            RedText
        ],
        content,
        autofocus,
        editorProps: {
            attributes: {
                class: `gh-comment-content focus:outline-0`,
                'data-testid': 'editor'
            }
        },
        parseOptions: {
            preserveWhitespace: 'full'
        }
    };
}

