import React from 'react';
import {Avatar} from '../Avatar';
import {Comment, useAppContext} from '../../../AppContext';
import {Editor, EditorContent} from '@tiptap/react';
import {Transition} from '@headlessui/react';
import {useCallback, useEffect, useRef, useState} from 'react';
// import {usePopupOpen} from '../../../utils/hooks';

type Progress = 'default' | 'sending' | 'sent' | 'error';
export type SubmitSize = 'small' | 'medium' | 'large';
type FormEditorProps = {
    submit: (data: {html: string}) => Promise<void>;
    progress: Progress;
    setProgress: (progress: Progress) => void;
    close?: () => void;
    reduced?: boolean;
    isOpen: boolean;
    editor: Editor | null;
    submitText: JSX.Element | null;
    submitSize: SubmitSize;
};

const FormEditor: React.FC<FormEditorProps> = ({submit, progress, setProgress, close, isOpen, editor, submitText}) => {
    const [charCount, setCharCount] = useState(0);
    const {member, dispatchAction} = useAppContext();
    const {t} = useAppContext();
    const limitOverText = t('140文字を超えています。');

    console.log("member", member);
    const memberName = member?.name ?? null;
    console.log("memberName", memberName);

    const openEditDetails = useCallback((options) => {
        editor?.commands?.blur();

        dispatchAction('openPopup', {
            type: 'addDetailsPopup',
            expertiseAutofocus: options.expertiseAutofocus ?? false,
            callback: function (succeeded: boolean) {
                if (!editor) {
                    return;
                }

                // useEffectは十分に早く反映されないため、手動で再度エディタにフォーカス
                if (!succeeded) {
                    return;
                }

                editor.setEditable(true);
                editor.commands.focus();
            }
        });
    }, [editor]);

    const editName = useCallback(() => {
        openEditDetails({expertiseAutofocus: false});
    }, [openEditDetails]);

    if (progress === 'sending') {
        submitText = null;
    }

    const stopIfFocused = useCallback((event) => {
        if (editor?.isFocused) {
            event.stopPropagation();
            return;
        }
    }, [editor]);

    const submitForm = useCallback(async () => {
        if (!editor || editor.isEmpty) {
            return;
        }

        setProgress('sending');

        try {
            await submit({
                html: editor.getHTML()
            });
        } catch (e) {
            setProgress('error');
            return;
        }

        if (close) {
            close();
        } else {
            setProgress('sent');
            editor.chain().clearContent().blur().run();
        }
        return false;
    }, [setProgress, editor, submit, close]);

    // エディタのコンテンツが変更されたら文字数をカウントし、141文字目以降にスタイルを適用
    useEffect(() => {
        if (!editor) {
            return;
        }

        const updateCharCount = () => {
            const text = editor.getText();
            setCharCount(text.length); // 文字数を取得

            // 141文字目以降に赤色を適用
            if (text.length > 140) {
                const safeText = text.slice(0, 140);
                const excessText = text.slice(140);

                // エディタの内容を安全に更新する
                editor.commands.setContent(`${safeText}<span style="color: red;">${excessText}</span>`, false);
            }
        };

        editor.on('update', updateCharCount);

        return () => {
            editor.off('update', updateCharCount);
        };
    }, [editor]);

    return (
        <div className={`relative w-full bg-white delay-100 duration-150`}>
            <div
                className={`w-full bg-white px-[28px] py-4 font-sans text-[14px] leading-normal tracking-wider transition-all delay-100 duration-150 focus:outline-0 ${isOpen ? 'min-h-[48px] cursor-text pb-[20px] pt-2' : 'min-h-[48px] cursor-pointer overflow-hidden '}`}
                data-testid="form-editor"
            >
                <EditorContent
                    className="font-sans text-[14px] tracking-widest text-neutral-600"
                    editor={editor}
                    onMouseDown={stopIfFocused}
                    onTouchStart={stopIfFocused}
                />
            </div>

            {/* 140文字を超えた場合の警告メッセージ */}
            {charCount > 140 && (
                <div className="mb-3 px-[28px] text-[10px] font-normal tracking-widest text-red-500">
                    {limitOverText}
                </div>
            )}

            {!memberName && (
                <div className="mb-3 px-[28px] text-[10px] font-normal tracking-widest text-red-500">
                    {t('コメントをするにはユーザーネームが必要です。')}
                </div>
            )}

            {!memberName && (
                <div className="mb-1 flex flex-col items-center justify-center space-y-2 px-[20px] transition-[opacity] duration-150">
                    <button
                        className={`flex justify-center w-full max-w-[400px] p-3 border bg-neutral-900 text-center font-sans text-[16px] font-normal text-white rounded-[30px] outline-0 transition-opacity duration-150`}
                        data-testid="submit-form-button"
                        disabled={charCount > 140} // 140文字を超えたらボタンを非活性にする
                        type="button"
                        onClick={editName}
                    >
                        <span>{t('ユーザーネームを登録してください')}</span>
                    </button>
                </div>
            )}

            {charCount > 0 && ( // 文字数が0より大きい場合のみボタンを表示
                <div className="mb-1 flex flex-col items-center justify-center space-y-2 px-[20px] transition-[opacity] duration-150">
                    <button
                        className={`flex justify-center w-full max-w-[400px] p-3 border text-center font-sans text-[16px] font-normal text-white rounded-[30px] outline-0 transition-opacity duration-150 ${
                            charCount > 140 ? 'bg-neutral-900 cursor-not-allowed opacity-10' : 'bg-neutral-900'
                        }`}
                        data-testid="submit-form-button"
                        disabled={charCount > 140} // 140文字を超えたらボタンを非活性にする
                        type="button"
                        onClick={submitForm}
                    >
                        <span>{submitText}</span>
                    </button>
                </div>
            )}
        </div>
    );
};

type FormHeaderProps = {
    show: boolean;
    name: string | null;
    expertise: string | null;
    editName: () => void;
    editExpertise: () => void;
};

const FormHeader: React.FC<FormHeaderProps> = ({show, name, editName}) => {
    return (
        <Transition
            enter="transition duration-500 delay-100 ease-in-out"
            enterFrom="opacity-0 -translate-x-2"
            enterTo="opacity-100 translate-x-0"
            leave="transition-none duration-0"
            leaveFrom="opacity-100"
            leaveTo="opacity-0"
            show={show}
        >
            <div
                className="font-sans text-[10px] font-normal tracking-normal text-neutral-800"
                data-testid="member-name"
                onClick={editName}
            >
                {name ? name : 'Anonymous'}
            </div>

        </Transition>
    );
};

type FormProps = {
    comment?: Comment;
    submit: (data: {html: string}) => Promise<void>;
    submitText: JSX.Element;
    submitSize: SubmitSize;
    close?: () => void;
    editor: Editor | null;
    reduced: boolean;
    isOpen: boolean;
};

const Form: React.FC<FormProps> = ({comment, submit, submitText, submitSize, close, editor, reduced}) => {
    const {member, dispatchAction} = useAppContext();
    // const isAskingDetails = usePopupOpen('addDetailsPopup');
    const [progress, setProgress] = useState<Progress>('default');
    const formEl = useRef(null);

    const memberName = member?.name ?? comment?.member?.name;
    const memberExpertise = member?.expertise ?? comment?.member?.expertise;

    // 常にフォームを開いた状態にするため、isOpenをtrueに固定
    const isOpen = true;

    const preventIfFocused = (event: React.SyntheticEvent) => {
        if (editor?.isFocused) {
            event.preventDefault();
            return;
        }
    };

    const openEditDetails = useCallback((options) => {
        editor?.commands?.blur();

        dispatchAction('openPopup', {
            type: 'addDetailsPopup',
            expertiseAutofocus: options.expertiseAutofocus ?? false,
            callback: function (succeeded: boolean) {
                if (!editor || !formEl.current) {
                    return;
                }

                // useEffectは十分に早く反映されないため、手動で再度エディタにフォーカス
                if (!succeeded) {
                    return;
                }

                editor.setEditable(true);
                editor.commands.focus();
            }
        });
    }, [editor, dispatchAction, formEl]);

    const editName = useCallback(() => {
        openEditDetails({expertiseAutofocus: false});
    }, [openEditDetails]);

    const editExpertise = useCallback(() => {
        openEditDetails({expertiseAutofocus: true});
    }, [openEditDetails]);

    const focusEditor = useCallback(() => {
        if (!editor) {
            return;
        }

        if (editor.isFocused) {
            return;
        }

        // メンバー名が未入力の場合は、名前の入力を強制
        if (!memberName) {
            editName();
            return;
        }

        editor.commands.focus();
    }, [editor, editName, memberName]);

    useEffect(() => {
        if (!editor) {
            return;
        }

        // メンバー名がない、または送信中の場合に編集を無効にする
        editor.setEditable(!!memberName && progress !== 'sending');
    }, [editor, memberName, progress]);

    return (
        <form ref={formEl} className={`-mx-3 mb-5 mt-[-10px]  rounded-md bg-white px-3 pb-2 pt-1 transition duration-200`} data-testid="form" onClick={focusEditor} onMouseDown={preventIfFocused} onTouchStart={preventIfFocused}>
            <div className="relative w-full">
                <div className="flex items-center justify-start gap-4 border-none bg-white px-[24px] py-2">
                    <Avatar comment={comment} />
                    <FormHeader editExpertise={editExpertise} editName={editName} expertise={memberExpertise} name={memberName} show={isOpen} />
                </div>
                <div className="bg-white font-sans leading-normal ">
                    <FormEditor close={close} editor={editor} isOpen={isOpen} progress={progress} reduced={reduced} setProgress={setProgress} submit={submit} submitSize={submitSize} submitText={submitText} />
                </div>
            </div>
        </form>
    );
};

export default Form;
