type UnpublishedCommentProps = {
    comment: Comment;
    openEditMode: () => void;
};

const UnpublishedComment: React.FC<UnpublishedCommentProps> = ({comment, openEditMode}) => {
    const {admin, t} = useAppContext();

    let notPublishedMessage;
    if (admin && comment.status === 'hidden') {
        notPublishedMessage = t('This comment has been hidden.');
    } else {
        notPublishedMessage = t('This comment has been removed.');
    }

    const avatar = (<BlankAvatar />);
    
    // header部分を定義
    const header = (
        <div className="flex items-start">
            <p className="mt-[4px] font-sans text-[16px] italic leading-normal text-[rgba(0,0,0,0.2)] dark:text-[rgba(255,255,255,0.35)]">
                {notPublishedMessage}
            </p>
            <div className="mt-[4px]">
                <MoreButton comment={comment} toggleEdit={openEditMode} />
            </div>
        </div>
    );

    // body部分を定義
    const body = (
        <RepliesContainer comment={comment} />
    );

    return (
        <CommentLayout avatar={avatar} body={body} header={header} />
    );
};

// Helper components

const MemberExpertise: React.FC<{comment: Comment}> = ({comment}) => {
    const {member} = useAppContext();
    const memberExpertise = member && comment.member && comment.member.uuid === member.uuid ? member.expertise : comment?.member?.expertise;

    if (!memberExpertise) {
        return null;
    }

    return (
        <span className="[overflow-wrap:anywhere]">{memberExpertise}<span className="mx-[0.3em]">·</span></span>
    );
};

const EditedInfo: React.FC<{comment: Comment}> = ({comment}) => {
    const {t} = useAppContext();
    if (!comment.edited_at) {
        return null;
    }
    return (
        <span>
            <span className="mx-[0.3em]">·</span>{t('Edited')}
        </span>
    );
};

const RepliesContainer: React.FC<{comment: Comment}> = ({comment}) => {
    const hasReplies = comment.replies && comment.replies.length > 0;

    if (!hasReplies) {
        return null;
    }

    return (
        <div className="mb-4 mt-10 sm:mb-0">
            <Replies comment={comment} />
        </div>
    );
};

