// TODO: move name detection to helper
const AuthorName: React.FC<{comment: Comment}> = ({comment}) => {
    const {t} = useAppContext();
    const name = !comment.member ? t('Deleted member') : (comment.member.name ? comment.member.name : t('Anonymous'));
    return (
        <h4 className="font-sans text-[10px] font-normal tracking-normal text-neutral-800">
            {name}
        </h4>
    );
};

const CommentHeader: React.FC<{comment: Comment}> = ({comment}) => {
    return (
        <div className="flex flex-col">
            <AuthorName comment={comment} />
            <div className="flex items-baseline pl-2 font-sans text-[14px] tracking-tight text-neutral-800">
                <MemberExpertise comment={comment}/>
                <EditedInfo comment={comment} />
            </div>
        </div>
    );
};

const CommentBody: React.FC<{html: string}> = ({html}) => {
    const dangerouslySetInnerHTML = {__html: html};
    return (
        <div className="mt mb-1 flex flex-row items-center gap-4">
            <p dangerouslySetInnerHTML={dangerouslySetInnerHTML} className="gh-comment-content break-all font-sans text-[14px] font-normal leading-normal tracking-widest text-neutral-600" data-testid="comment-content"/>
        </div>
    );
};

