type CommentLayoutProps = {
    avatar: React.ReactNode;
    body: React.ReactNode;
    header: React.ReactNode;
};

const CommentLayout: React.FC<CommentLayoutProps> = ({avatar, header, body}) => {
    return (
        <div className="flex w-full flex-col gap-2 rounded-[8px] bg-white p-[12px] " data-testid="comment-component">
            <div className="flex items-center gap-2">
                <div className="mr-3 flex">
                    {avatar}
                </div>
                <div className="grow">
                    {header}
                </div>
            </div>
            <div className="mt-1 pl-2">
                {body}
            </div>
        </div>
    );
};

