import {useAppContext} from '../../AppContext';

const Pagination = () => {
    const {pagination, dispatchAction} = useAppContext();

    const loadMore = () => {
        dispatchAction('loadMoreComments', {});
    };

    if (!pagination) {
        return null;
    }

    const left = pagination.total - pagination.page * pagination.limit;

    if (left <= 0) {
        return null;
    }

    const text = 'Load more';

    return (
        <button className="text-md group mx-auto mb-10 flex  px-0 pb-2 pt-0  text-left font-sans font-semibold text-white" data-testid="pagination-component" type="button" onClick={loadMore}>
            <span className="mt-3 flex h-[39px] w-full items-center justify-center whitespace-nowrap font-bold tracking-widest text-white hover:cursor-pointer">{text}</span>
        </button>
    );
};

export default Pagination;
